align-items: center;
justify-content: center;
}

/* Remove extra left and right margins, due to padding */
.row {margin: 0px -5px;}

/* Clear floats after the columns */
.row:after {
  content: "";
  display: table;
  clear: both;
}

/* Responsive columns */
@media screen and (max-width: 1000px) {
  .column {
    width: 100%;
    display: block;
