.blog-card {  
  flex-direction: column;
  margin: 2rem auto;
  box-shadow: 0 3px 7px -1px rgba(0, 0, 0, .1);
  margin-bottom: 1.6%;
  background: #fff;
  line-height: 1.4;
  font-family: sans-serif;
  border-radius: 5px;
  overflow: hidden;
  z-index: 0; 
}
.blog-card {
  color: inherit;
}

.blog-card:hover .photo {
  transform: scale(1.3) rotate(3deg);
}
.blog-card .meta {
  position: relative;
  z-index: 0;
  height: 300px;
}
.blog-card .photo {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background-size: cover;
  background-position: center;
  transition: transform 0.2s;
}
.blog-card .details, .blog-card .details ul {
  margin: auto;
  padding: 0;
  list-style: none;
}
.blog-card .details {
  position: absolute;
  top: 0;
  bottom: 0;
  left: -100%;
  margin: auto;
  transition: left 0.2s;
  background: rgba(0, 0, 0, .6);
  color: #fff;
  padding: 10px;
  width: 100%;
  font-size: 0.9rem;
}
.blog-card a{
color: #FFFFFF;text-decoration: none!important;
}
.blog-card h1{
color: black;
 
align-items: center; 
letter-spacing: 3px; 
text-transform: uppercase; 
}
.blog-card .details a {
  text-decoration: dotted underline;
}
.blog-card .details ul li {
  display: inline-block;
}
.blog-card .details .author:before {
  font-family: FontAwesome;
  margin-right: 10px; 
}
.blog-card .details .date:before {
   
  font-family: FontAwesome;
  margin-right: 10px; 
}
.blog-card .details .tags ul:before {
  font-family: FontAwesome; 
  margin-right: 10px;
}
.blog-card .details .tags li {
  margin-right: 2px;
}
.blog-card .details .tags li:first-child {
  margin-left: -4px;
}
.blog-card .description {
  padding: 1rem;
  background: #fff;
  position: relative;
  z-index: 1;
  text-decoration: none;
}
.date{
font-size: 18px;
}
.blog-card .description h1, .blog-card .description h2 {
  font-family: Poppins, sans-serif;
  text-decoration: none;
}
.blog-card .description h1 {
  line-height: 1;
  margin: 0;
  font-size: 1.7rem;
  text-decoration: none;
}
.blog-card .description h2 {
  font-size: 1rem;
  font-weight: 300;
  text-transform: uppercase;
  color: #a2a2a2;
  margin-top: 5px;
  text-decoration: none;
}

.blog-card .description .read-more {
  color: #5ad67d !important;
  display: inline-block !important;
  position: relative !important;
  font-size: 16px;
  text-transform: uppercase;
  border:none;
  background-color: transparent;
  text-decoration: none;
}

.blog-card .description .read-more:hover:after {
  margin-left: 5px;
  opacity: 1;
  text-decoration: none;
}
.blog-card p {
  position: relative;
  margin: 1rem 0 0;
}
.blog-card p:first-of-type {
  margin-top: 1.25rem;
}
.blog-card p:first-of-type:before {
  content: "";
  position: absolute;
  height: 5px;
  background: #5ad67d;
  width: 35px;
  top: -0.75rem;
  border-radius: 3px;
}
.blog-card:hover .details {
  left: 0%;
}
@media (min-width: 640px) {
  .blog-card {
      flex-direction: row;
      max-width: 700px;
 }
  .blog-card .meta {
      flex-basis: 40%;
      height: auto;
 }
  .blog-card .description {
      flex-basis: 60%;
      text-decoration: none;
 }
  .blog-card .description:before {
      transform: skewX(-3deg);
      content: "";
      background: #fff;
      width: 30px;
      position: absolute;
      left: -10px;
      top: 0;
      bottom: 0;
      z-index: -1;
      text-decoration: none;
 }
  .blog-card.alt {
      flex-direction: row-reverse;
 }
  .blog-card.alt .description:before {
      left: inherit;
      right: -10px;
      transform: skew(3deg);
      text-decoration: none;
 }
  .blog-card.alt .details {
      padding-left: 25px;
 }
}
